.HomePage {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__content {
    width: 90%;
    max-width: 920px;
    height: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    margin-bottom: 4rem;
    border-radius: 0rem 0rem 4rem 4rem;
    .venueSection,
    .eventsSection,
    .introSection {
      padding: 30px;

      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
    }
    .introSection {
      .ganpatiIcon {
        width: 140px;
      }
      .inviteText {
        max-width: 70%;
        font-family: "Cormorant Upright", sans-serif;
        font-size: 32px;
        font-weight: 500;
        line-height: 48px;
        text-align: center;
        color: rgba(170, 127, 89, 1);
        margin: 1.5rem 0rem;
      }
      .coupleImage {
        max-width: 250px;
        margin-bottom: 1.5rem;
      }

      .coupleNames {
        display: flex;
        width: 100%;
        justify-content: space-around;
        align-items: center;
        .individualName {
          margin: 0;
          font-family: "Great Vibes", sans-serif;
          font-size: 108px;
          font-weight: 400;
          line-height: 135px;
          letter-spacing: 0em;
          text-align: left;
          color: rgba(216, 129, 0, 1);
        }
        .andText {
          margin: 0;

          font-family: "Brawler", sans-serif;
          font-size: 64px;
          font-weight: 400;
          line-height: 86px;
          letter-spacing: 0em;
          text-align: left;
          color: rgba(189, 145, 106, 1);
        }
      }
    }
    .eventsSection {
      background: rgba(253, 189, 94, 0.12);
      padding-top: 64px;
      padding-bottom: 64px;
      &:nth-child(2) {
        padding-bottom: 0px;
      }
      .eventIcon {
        width: 100px;
        max-width: 100px;
        margin-bottom: 0.75rem;
      }
      .eventName {
        font-family: "Cormorant Upright", sans-serif;
        font-size: 64px;
        font-weight: 600;
        line-height: 78px;
        letter-spacing: 0em;
        text-align: center;
        color: rgba(182, 115, 36, 1);
        margin: 0;
        margin-bottom: 0.75rem;
      }
      .eventDetails {
        margin: 0;
        font-family: "Inter", sans-serif;
        font-size: 32px;
        font-weight: 400;
        line-height: 45px;
        letter-spacing: 0em;
        text-align: center;
        color: rgba(182, 115, 36, 1);
        strong {
          font-weight: 500;
        }
      }
    }
    .venueSection {
      padding-top: 4rem;
      padding-bottom: 4rem;
      .heading {
        font-family: "Bonheur Royale", sans-serif;
        font-size: 81px;
        font-weight: 400;
        line-height: 93px;
        letter-spacing: 0em;
        text-align: center;
        color: rgba(189, 145, 106, 1);
        margin: 0;
      }
      .venueLocation {
        width: 80%;
        max-width: 80%;
        height: 300px;
        background: rgba(242, 242, 242, 1);
        border-radius: 0.5rem;
        margin-bottom: 1.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid rgba(196, 196, 196, 1);
        overflow: hidden;
        iframe {
          width: 100%;
          height: 100%;
        }
      }
      .venueName {
        margin: 0;
        font-family: "Inter", sans-serif;
        font-size: 32px;
        font-weight: 600;
        line-height: 45px;
        letter-spacing: 0em;
        color: rgba(189, 145, 106, 1);
        text-align: center;
        margin-bottom: 1rem;
      }
      .venueAddress {
        margin: 0;
        font-family: "Inter", sans-serif;
        font-size: 27px;
        font-weight: 400;
        line-height: 41px;
        letter-spacing: 0em;
        text-align: center;
        color: rgba(189, 145, 106, 1);
      }
    }
  }
}

@media screen and (min-device-width: 540px) and (max-device-width: 1024px) {
  .HomePage {
    &__content {
      width: 80%;

      .introSection {
        .coupleNames {
          flex-direction: column;
          .individualName {
            font-size: 80px;
            line-height: 100px;
          }
          .andText {
            font-size: 48px;
            line-height: 86px;
          }
        }
      }
      .eventsSection {
        &:nth-child(2) {
        }
        .eventIcon {
        }
        .eventName {
          font-size: 64px;
          line-height: 78px;
        }
        .eventDetails {
          font-size: 24px;
          line-height: 34px;
          strong {
          }
        }
      }
      .venueSection {
        .heading {
          font-size: 64px;
          line-height: 74px;
        }
        .venueLocation {
          iframe {
          }
        }
        .venueName {
          font-size: 32px;
          line-height: 45px;
        }
        .venueAddress {
          font-size: 24px;
          line-height: 36px;
        }
      }
    }
  }
}

/* For Mobile Portrait View */
@media screen and (max-device-width: 540px) {
  .HomePage {
    &__content {
      .introSection {
        .inviteText {
          max-width: 90%;
          font-size: 24px;
          line-height: 36px;
        }
        .coupleNames {
          flex-direction: column;
          .individualName {
            font-size: 64px;
            line-height: 80px;
          }
          .andText {
            font-size: 48px;
            line-height: 86px;
          }
        }
      }
      .eventsSection {
        &:nth-child(2) {
        }
        .eventIcon {
          width: 64px;
          max-width: 64px;
          // margin-bottom: 0.75rem;
        }
        .eventName {
          font-size: 36px;
          line-height: 48px;
        }
        .eventDetails {
          font-size: 16px;
          line-height: 24px;
          strong {
          }
        }
      }
      .venueSection {
        .heading {
          font-size: 48px;
          line-height: 64px;
        }
        .venueLocation {
          iframe {
          }
        }
        .venueName {
          font-size: 24px;
          line-height: 36px;
        }
        .venueAddress {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
  }
}
