.HomePage {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.HomePage__content {
  width: 90%;
  max-width: 920px;
  height: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #fff;
  margin-bottom: 4rem;
  border-radius: 0rem 0rem 4rem 4rem;
}
.HomePage__content .venueSection,
.HomePage__content .eventsSection,
.HomePage__content .introSection {
  padding: 30px;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.HomePage__content .introSection .ganpatiIcon {
  width: 140px;
}
.HomePage__content .introSection .inviteText {
  max-width: 70%;
  font-family: "Cormorant Upright", sans-serif;
  font-size: 32px;
  font-weight: 500;
  line-height: 48px;
  text-align: center;
  color: rgb(170, 127, 89);
  margin: 1.5rem 0rem;
}
.HomePage__content .introSection .coupleImage {
  max-width: 250px;
  margin-bottom: 1.5rem;
}
.HomePage__content .introSection .coupleNames {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
}
.HomePage__content .introSection .coupleNames .individualName {
  margin: 0;
  font-family: "Great Vibes", sans-serif;
  font-size: 108px;
  font-weight: 400;
  line-height: 135px;
  letter-spacing: 0em;
  text-align: left;
  color: rgb(216, 129, 0);
}
.HomePage__content .introSection .coupleNames .andText {
  margin: 0;
  font-family: "Brawler", sans-serif;
  font-size: 64px;
  font-weight: 400;
  line-height: 86px;
  letter-spacing: 0em;
  text-align: left;
  color: rgb(189, 145, 106);
}
.HomePage__content .eventsSection {
  background: rgba(253, 189, 94, 0.12);
  padding-top: 64px;
  padding-bottom: 64px;
}
.HomePage__content .eventsSection:nth-child(2) {
  padding-bottom: 0px;
}
.HomePage__content .eventsSection .eventIcon {
  width: 100px;
  max-width: 100px;
  margin-bottom: 0.75rem;
}
.HomePage__content .eventsSection .eventName {
  font-family: "Cormorant Upright", sans-serif;
  font-size: 64px;
  font-weight: 600;
  line-height: 78px;
  letter-spacing: 0em;
  text-align: center;
  color: rgb(182, 115, 36);
  margin: 0;
  margin-bottom: 0.75rem;
}
.HomePage__content .eventsSection .eventDetails {
  margin: 0;
  font-family: "Inter", sans-serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 45px;
  letter-spacing: 0em;
  text-align: center;
  color: rgb(182, 115, 36);
}
.HomePage__content .eventsSection .eventDetails strong {
  font-weight: 500;
}
.HomePage__content .venueSection {
  padding-top: 4rem;
  padding-bottom: 4rem;
}
.HomePage__content .venueSection .heading {
  font-family: "Bonheur Royale", sans-serif;
  font-size: 81px;
  font-weight: 400;
  line-height: 93px;
  letter-spacing: 0em;
  text-align: center;
  color: rgb(189, 145, 106);
  margin: 0;
}
.HomePage__content .venueSection .venueLocation {
  width: 80%;
  max-width: 80%;
  height: 300px;
  background: rgb(242, 242, 242);
  border-radius: 0.5rem;
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(196, 196, 196);
  overflow: hidden;
}
.HomePage__content .venueSection .venueLocation iframe {
  width: 100%;
  height: 100%;
}
.HomePage__content .venueSection .venueName {
  margin: 0;
  font-family: "Inter", sans-serif;
  font-size: 32px;
  font-weight: 600;
  line-height: 45px;
  letter-spacing: 0em;
  color: rgb(189, 145, 106);
  text-align: center;
  margin-bottom: 1rem;
}
.HomePage__content .venueSection .venueAddress {
  margin: 0;
  font-family: "Inter", sans-serif;
  font-size: 27px;
  font-weight: 400;
  line-height: 41px;
  letter-spacing: 0em;
  text-align: center;
  color: rgb(189, 145, 106);
}

@media screen and (min-device-width: 540px) and (max-device-width: 1024px) {
  .HomePage__content {
    width: 80%;
  }
  .HomePage__content .introSection .coupleNames {
    flex-direction: column;
  }
  .HomePage__content .introSection .coupleNames .individualName {
    font-size: 80px;
    line-height: 100px;
  }
  .HomePage__content .introSection .coupleNames .andText {
    font-size: 48px;
    line-height: 86px;
  }
  .HomePage__content .eventsSection .eventName {
    font-size: 64px;
    line-height: 78px;
  }
  .HomePage__content .eventsSection .eventDetails {
    font-size: 24px;
    line-height: 34px;
  }
  .HomePage__content .venueSection .heading {
    font-size: 64px;
    line-height: 74px;
  }
  .HomePage__content .venueSection .venueName {
    font-size: 32px;
    line-height: 45px;
  }
  .HomePage__content .venueSection .venueAddress {
    font-size: 24px;
    line-height: 36px;
  }
}
/* For Mobile Portrait View */
@media screen and (max-device-width: 540px) {
  .HomePage__content .introSection .inviteText {
    max-width: 90%;
    font-size: 24px;
    line-height: 36px;
  }
  .HomePage__content .introSection .coupleNames {
    flex-direction: column;
  }
  .HomePage__content .introSection .coupleNames .individualName {
    font-size: 64px;
    line-height: 80px;
  }
  .HomePage__content .introSection .coupleNames .andText {
    font-size: 48px;
    line-height: 86px;
  }
  .HomePage__content .eventsSection .eventIcon {
    width: 64px;
    max-width: 64px;
  }
  .HomePage__content .eventsSection .eventName {
    font-size: 36px;
    line-height: 48px;
  }
  .HomePage__content .eventsSection .eventDetails {
    font-size: 16px;
    line-height: 24px;
  }
  .HomePage__content .venueSection .heading {
    font-size: 48px;
    line-height: 64px;
  }
  .HomePage__content .venueSection .venueName {
    font-size: 24px;
    line-height: 36px;
  }
  .HomePage__content .venueSection .venueAddress {
    font-size: 16px;
    line-height: 24px;
  }
}/*# sourceMappingURL=HomePage.css.map */